.contacts {

  background-color: $color-bg-white;
  &__wrapper {
    width: calc(100% - 9rem);
    margin: 0 auto;
    padding-bottom: 5.25rem;
  }

  &__title {
    color: $color-main;
    font-size: 2.25rem;
    font-weight: 900;
    line-height: 3.813rem;
    text-transform: uppercase;

    padding-top: 2.063rem;
    // margin-left: 1.125rem;
    margin:  0 auto;

    width: calc(100% - 9rem);
  }

  &__content {
    display: flex;
    color: $color-text;

    width: calc(100% - 9rem);
    margin: 4.375rem auto 0 auto;
  }

  &__left,
  &__right {
    width: 50%;
  }

  &__left {
    display: flex;
    flex-direction: column;
  }

  &__info {
    display: flex;
    flex-direction: column;
  }

  &__address,
  &__phone,
  &__email {
    font-size: 2.25rem;
    font-weight: 700;
    font-style: normal;
    line-height: 3.75rem;
  }

  &__phone,
  &__email {
    text-decoration: none;
    color: $color-text;
    margin-top: 0.375rem;
    width: max-content;
  }

  &__map {
    width: 28.5rem;
    height: 17rem;
    box-shadow: 0.375rem 0.375rem 1.25rem rgba(0, 0, 0, 0.25);
    border-radius: 1.25rem;

    margin-top: 2.375rem;
  }

  .form {
    display: flex;
    flex-direction: column;
    margin-right: 1.25rem;

    &__name,
    &__email,
    &__message {
      font-size: 1.5rem;
      line-height: 2.438rem;
      font-weight: 700;
      margin-top: 1.25rem;
    }

    input {
      outline: none;
      border: none;
      border-bottom: 0.125rem solid $color-text;
    }

    input,
    textarea {
      font-size: 1.125rem;
      line-height: 1.813rem;
    }

    textarea {
      &::placeholder {
        color: rgba(0, 0, 0, 0.7);
      }

      position: relative;
      height: 100%;
      max-height: 9.125rem;
      resize: none;
      background: url('../img/black-line.png') 0 -0.188rem;
      outline: 0;
      border: none;
    }

    &__distribution,
    &__consent {
      font-size: 0.75rem;
      line-height: 1.188rem;

      &:hover {
        cursor: pointer;
      }

      input[type='checkbox'] {
        position: absolute;
        z-index: -10;
        opacity: 0;
      }

      span {
        display: inline-flex;
        align-items: center;
        user-select: none;

        &::before {
          content: '';
          display: inline-block;
          width: 0.75rem;
          height: 0.75rem;
          border: 0.063rem solid $color-text;
          margin-right: 0.5em;
          background-color: $color-text;
        }
      }

      input:not(:disabled):active + span::before {
        background-color: #b3d7ff;
        border-color: #b3d7ff;
      }

      input:checked + span::before {
        border-color: $color-text;
        background-color: $color-main;
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fefefe' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
      }
    }

    &__distribution {
      margin-top: 1.125rem;
    }

    &__consent {
      margin-top: 0.438rem;

      a {
        font-weight: 700;
        color: $color-text;
        margin-left: 0.125rem;
      }
    }

    &__submit {
      button {
        color: $color-text-white;
        background-color: $color-main;
        border-radius: 1.25rem;
        border: none;

        font-size: 1rem;
        font-weight: 700;

        padding: 0.813rem 4rem;
        box-shadow: $button-shadow;
        outline: 0;

        margin-top: 1.875rem;

        &:hover {
          cursor: pointer;
        }
      }
    }
  }

  &__links {
    display: flex;
    margin: 4.688rem auto 0 auto;
    width: calc(100% - 9rem);
  }

  .links {
    &__column {
      display: flex;
      flex-direction: column;
      width: 80%;

      &:last-child {
        width: 20%;
        align-items: center;
        justify-content: center;
      }
    }

    &__row {
      display: flex;
    }

    &__link {
      width: 25%;
      color: $color-text;
      font-size: 1.5rem;
      font-weight: 700;
      line-height: 2.438rem;
      text-decoration: none;

      &:nth-child(3) {
        width: 20%;
      }
    }
  }

  .social {
    display: flex;

    a {
      display: block;
      
      
      &:not(:last-child) {
        margin-right: 0.875rem;
      }
    }

    svg {
      width: 2.5rem;
      height: 2.5rem;
      border-radius: 50%;
      box-shadow: $button-shadow;
    }
  }
}
