body {
  font-family: 'Axiforma', sans-serif;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

source {
  width: 0;
  height: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
}

.header {
  height: 3.125rem;
  margin: 0 8.563rem 0 1.25rem;
  padding-top: 2rem;
  padding-bottom: 0.625rem;
  position: relative;
  z-index: 50;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  max-width: 90rem;
}

.header svg {
  height: 100%;
}

.header a {
  font-size: 1.125rem;
  font-weight: 700;
  color: #d40f14;
  height: inherit;
  display: flex;
  align-items: center;
  text-decoration: none;
}

.header__logo {
  height: inherit;
  display: flex;
  z-index: 10;
}

.header__logo svg {
  width: 11.5rem;
}

.header__navigation {
  width: 100%;
  margin: 0 2.375rem 0 3.313rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: inherit;
}

.header__contacts, .header__for {
  width: 50%;
  height: inherit;
  display: flex;
}

.header__contacts a:not(:first-child), .header__for a:not(:first-child) {
  margin-left: 2.188rem;
}

.header-contacts {
  justify-content: flex-end;
}

.header-contacts svg {
  width: 1.125rem;
  margin-right: 0.625rem;
}

.sidebar {
  position: fixed;
  width: 7.375rem;
  height: 100vh;
  border-left: 0.063rem solid #d40f14;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  top: 0;
  right: 0;
  z-index: 5;
}

.sidebar__blank {
  width: 7.375rem;
  height: 6.5rem;
}

.sidebar__request {
  width: 5.438rem;
  height: 5.438rem;
  background-color: #d40f14;
  border-radius: 50%;
  font-size: 0.75rem;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 0 0 0 0 black;
  transform: scale(1);
  animation-play-state: paused;
}

.sidebar__request:hover {
  cursor: pointer;
  animation: pulse 2s infinite;
}

.sidebar__request p {
  margin: 0;
  height: 2.125rem;
}

.sidebar__arrow-top {
  margin-bottom: 6.875rem;
}

.sidebar__arrow-top svg {
  height: 2.75rem;
  width: 2.75rem;
}

.sidebar__arrow-top .arrow-use path {
  animation: arrow-top 2s linear infinite;
}

.sidebar__arrow-top:hover {
  cursor: pointer;
}

.burger-wrapper {
  width: 7.375rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 52;
}

.burger-wrapper .burger {
  position: fixed;
  z-index: 10;
  top: 2rem;
  width: 2.75rem;
  height: 2.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.125rem solid #d40f14;
  border-radius: 50%;
  transition: border-color 0.4s ease;
}

.burger-wrapper .burger:hover {
  cursor: pointer;
}

.burger-wrapper .burger span {
  position: absolute;
  content: '';
  max-width: 1.688rem;
  width: 100%;
  height: 0.125rem;
  display: block;
  background-color: #d40f14;
  transition: transform 0.4s ease, background-color 0.4s ease, opacity 0.2s ease, margin 0.5s ease;
}

.burger-wrapper .burger span:first-child {
  margin-bottom: 0.875rem;
}

.burger-wrapper .burger span:last-child {
  margin-top: 0.875rem;
}

.dropdown {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: #d40f14;
  top: 0;
  left: 0;
  transform: translateY(-250%);
  transition: transform 0.4s ease-in-out;
  z-index: 51;
}

.dropdown.down {
  transform: translateY(0);
  transition: transform 0.4 ease-in-out;
}

.dropdown__header {
  display: flex;
  align-items: center;
  height: 3.125rem;
  margin: 2rem 8.563rem 0 1.25rem;
}

.dropdown__header svg {
  width: 11.5rem;
  height: inherit;
}

.dropdown__menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 80%;
  margin-top: 2.875rem;
}

.dropdown__item {
  font-size: 3rem;
  font-weight: 700;
  line-height: 3.5rem;
  text-transform: uppercase;
  color: transparent;
  -webkit-text-stroke: 2px #fff;
  transition: color 0.4s ease;
}

.dropdown__item:not(:first-child) {
  margin-top: 2rem;
}

.dropdown__item:hover {
  cursor: pointer;
  transition: color 0.4s ease;
  color: #fff;
}

.dropdown__right-side {
  position: absolute;
  right: 0;
  top: 0;
  width: 7.375rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.dropdown__social {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 17.875rem;
}

.dropdown__social :not(:first-child) {
  margin-top: 0.938rem;
}

.dropdown__social a {
  width: 2.5rem;
  height: 2.5rem;
}

.dropdown__social svg {
  width: inherit;
  height: inherit;
  border-radius: 1.25rem;
  box-shadow: 0.25rem 0.25rem 0.938rem rgba(0, 0, 0, 0.2);
}

.dropdown__contacts {
  width: 13.375rem;
  display: flex;
  flex-direction: column;
  position: absolute;
  margin-bottom: 5.7rem;
  margin-right: 25.75rem;
}

.dropdown__contacts a {
  display: flex;
  align-items: center;
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 1.875rem;
  text-decoration: none;
  color: #fff;
}

.dropdown__contacts svg {
  width: 1.125rem;
  height: 1.125rem;
  margin-right: 0.625rem;
}

.center-line {
  position: fixed;
  z-index: -4;
  height: 100vh;
  top: 0;
  left: 50%;
  border-right: 0.063rem solid #d40f14;
}

@keyframes arrow-top {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes pulse {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(215, 15, 22, 0.7);
  }
  70% {
    transform: scale(1);
    box-shadow: 0 0 0 10px rgba(215, 15, 22, 0);
  }
  100% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(215, 15, 22, 0);
  }
}

.decoy {
  width: 100%;
}

.decoy__wrapper {
  position: relative;
  width: calc(100% - 14.75rem);
  margin: 16.25rem auto 0 auto;
}

.decoy__title {
  font-size: 3rem;
  max-width: 72.625rem;
  margin: 0 auto;
  color: #111;
  line-height: 4.938rem;
}

.decoy__title span {
  color: #fff;
  -webkit-text-stroke: 2px #d40f14;
  transition: color 0.3s ease;
}

.decoy__title span:hover {
  transition: color 0.3s ease;
  cursor: pointer;
  color: #d40f14;
}

.decoy__popup-employer, .decoy__popup-aspirant {
  position: absolute;
  width: 21.5rem;
  height: 25.5rem;
  top: calc(50% + 1rem);
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  opacity: 0;
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: 0.375rem 0.375rem 1.25rem rgba(0, 0, 0, 0.25);
  border: 0.375rem solid #d40f14;
  border-radius: 1.25rem;
  border-radius: 1.25rem;
  object-fit: cover;
}

.decoy__popup-employer.active, .decoy__popup-aspirant.active {
  opacity: 1;
}

.decoy__popup-aspirant {
  background-image: url("../img/popup-aspirant.jpg");
}

.decoy__popup-employer {
  background-image: url("../img/popup-employer.jpg");
}

.about {
  width: 100%;
  color: #111;
}

.about__wrapper {
  display: flex;
  flex-direction: column;
  width: calc(100% - 14.75rem);
  margin: 17.125rem auto 0 auto;
}

.about__info {
  display: flex;
  margin: 4.375rem 1.563rem 0 1.875rem;
}

.about__title {
  font-size: 2.25rem;
  font-weight: 900;
  text-transform: uppercase;
  color: #d40f14;
  margin-left: 1.875rem;
}

.about .right-content__title {
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 2.438rem;
}

.about .right-content__description {
  font-size: 1.25rem;
  line-height: 1.625rem;
  margin-top: 2.75rem;
}

.about .right-content__description p:not(:first-child) {
  margin-top: 2.5rem;
}

.about .right-content__button {
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 1.875rem;
  color: #d40f14;
  border: 0.25rem solid #d40f14;
  border-radius: 1.25rem;
  background-color: #fff;
  box-shadow: 0.188rem 0.188rem 0.938rem #00000033;
  margin-top: 4.813rem;
  padding: 0.688rem 2.625rem;
  transition: color 0.4s ease, background-color 0.4s ease;
}

.about .right-content__button:hover {
  cursor: pointer;
  color: #fff;
  background-color: #d40f14;
  transition: color 0.4s ease, background-color 0.4s ease;
}

.about .left-content__images {
  position: relative;
  display: flex;
  flex-direction: column;
}

.about .left-content__image-bottom, .about .left-content__image-top {
  max-width: 28.125rem;
  width: 100%;
  position: relative;
  box-shadow: 0.375rem 0.375rem 1.25rem rgba(0, 0, 0, 0.25);
  border-radius: 1.25rem;
}

.about .left-content__image-top {
  z-index: 2;
  top: -8.375rem;
  left: 7.125rem;
}

.about__left {
  width: 60%;
}

.about__right {
  width: 40%;
}

.team {
  background-color: #d40f14;
}

.team__wrapper {
  width: calc(100% - 14.75rem);
  margin: 0 auto;
}

.team__title {
  font-size: 2.25rem;
  font-weight: 900;
  line-height: 3.813rem;
  text-transform: uppercase;
  color: #fff;
  padding-top: 2.063rem;
  margin-left: 1.875rem;
}

.team__text {
  margin-top: 4.375rem;
  margin-left: 4.563rem;
  max-width: 28.563rem;
}

.team__text p {
  color: #fff;
  font-size: 1.25rem;
  line-height: 1.813rem;
}

.team__info {
  display: flex;
  margin: 0 auto;
}

.team__right {
  width: 55%;
}

.team__left {
  width: 45%;
}

.team .right-content__images {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 4.375rem;
  margin-right: 1.25rem;
}

.team .right-content__image-bottom, .team .right-content__image-top {
  width: 28.125rem;
  position: relative;
  box-shadow: 0.375rem 0.375rem 1.25rem rgba(0, 0, 0, 0.25);
  border-radius: 1.25rem;
}

.team .right-content__image-top {
  z-index: 2;
  top: -8.75rem;
  right: 7.625rem;
}

.team__button {
  display: inline-block;
  text-decoration: none;
  color: #fff;
  background-color: #d40f14;
  border: 0.25rem solid #fff;
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 1.875rem;
  border-radius: 1.25rem;
  padding: 0.688rem 2.625rem;
  margin-top: 5rem;
  box-shadow: 0.188rem 0.188rem 0.938rem #00000033;
  transition: color 0.4s ease, background-color 0.4s ease;
}

.team__button:hover {
  cursor: pointer;
  transition: color 0.4s ease, background-color 0.4s ease;
  color: #d40f14;
  background-color: #fff;
}

.left-side-bar {
  position: fixed;
  width: 7.375rem;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 0;
  background-color: #fff;
  border-right: 0.063rem solid #d40f14;
}

.slider {
  background-color: #fff;
}

.slider__wrapper {
  width: calc(100% - 14.75rem);
  margin: 0 auto;
  padding: 6.5rem 0;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slider__title {
  text-transform: uppercase;
  color: #d40f14;
  font-size: 2.25rem;
  font-weight: 900;
  align-self: flex-start;
  margin-left: 1.875rem;
}

.slider .swiper-container {
  width: 71.85rem;
  height: 100%;
  max-height: 26.25rem;
  margin: 5.625rem auto 0 auto;
  padding-left: 0.375rem;
  overflow-x: hidden;
}

.slider .swiper-wrapper {
  display: flex;
  align-items: center;
}

.slider .slide {
  width: 23.5rem;
  height: 26rem;
  background-image: url("../img/slider-bg.webp");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 1.25rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.slider .slide__date {
  color: #fff;
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 2.438rem;
  padding: 1.25rem 0 0 1.25rem;
}

.slider .swiper-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2.5rem;
}

.slider .swiper-pagination-bullet {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  border: 0.125rem solid #d40f14;
  border-radius: 50%;
}

.slider .swiper-pagination-bullet-active {
  background-color: #d40f14;
}

.slider .swiper-pagination-bullet:hover {
  cursor: pointer;
}

.slider .swiper-pagination-bullet:not(:last-child) {
  margin-right: 0.625rem;
}

.slider .substrate {
  background-color: rgba(255, 255, 255, 0.8);
  padding: 1.25rem 1.25rem 0.938rem 1.25rem;
  border-top-left-radius: 1.25rem;
  border-top-right-radius: 1.25rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.slider .substrate__title {
  font-weight: 700;
  font-size: 1.45rem;
  line-height: 2.438rem;
  color: #111;
  text-align: center;
}

.slider .substrate__description {
  width: 100%;
  font-size: 1.125rem;
  line-height: 1.625rem;
  text-align: center;
}

.slider .substrate__footer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 1.375rem;
}

.slider .substrate__button {
  font-size: 1.125rem;
  line-height: 1.875rem;
  font-weight: 700;
  box-shadow: 0.188rem 0.188rem 0.938rem #00000033;
  border: 0.25rem solid #fff;
  border-radius: 1.25rem;
  background-color: #d40f14;
  color: #fff;
  padding: 0.688rem 1.063rem;
  transition: color 0.4s ease, background-color 0.4s ease;
}

.slider .substrate__button:hover {
  cursor: pointer;
  color: #d40f14;
  background-color: #fff;
  transition: color 0.4s ease, background-color 0.4s ease;
}

.slider .substrate__arrow-link {
  width: 3.25rem;
  height: 3.25rem;
  margin-left: 1.25rem;
}

.slider .substrate__arrow-link svg path {
  transition: stroke 0.4s ease;
}

.slider .substrate__arrow-link svg:hover {
  cursor: pointer;
}

.slider .substrate__arrow-link svg:hover path {
  transition: stroke 0.4s ease;
  stroke: white;
}

.slider__button {
  color: #d40f14;
  background-color: #fff;
  text-decoration: none;
  border: 0.25rem solid #d40f14;
  border-radius: 1.25rem;
  padding: 0.688rem 1.625rem;
  font-size: 1.125rem;
  font-weight: 700;
  margin-top: 2.5rem;
  transition: background-color .4s ease, color .4s ease;
}

.slider__button:hover {
  cursor: pointer;
  color: #fff;
  background-color: #d40f14;
}

.partners {
  background-color: #d40f14;
}

.partners__wrapper {
  width: calc(100% - 14.75rem);
  margin: 0 auto;
}

.partners__title {
  font-size: 2.25rem;
  font-weight: 900;
  line-height: 3.813rem;
  color: #fff;
  text-transform: uppercase;
  margin-left: 1.875rem;
  padding-top: 2.125rem;
}

.partners__container {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.partners__left, .partners__right {
  width: 50%;
  display: flex;
}

.partners__left {
  width: 40%;
  justify-content: space-between;
  margin-top: 5.375rem;
  margin-bottom: 9.063rem;
}

.partners__content-column {
  width: 12.625rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.partners__content-column svg {
  max-width: 11.5rem;
  margin-top: 1.375rem;
}

.partners__content-column svg:nth-child(4) {
  max-width: 5.375rem;
}

.partners__right {
  overflow-y: hidden;
  position: relative;
  height: 56.25rem;
}

.partners__gradient-bottom, .partners__gradient-top {
  position: absolute;
  width: 100%;
  height: 8.625rem;
}

.partners__gradient-bottom {
  bottom: -1px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #d40f14 100%);
}

.partners__gradient-top {
  top: -1px;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0) 0%, #d40f14 100%);
}

.partners-swiper {
  width: 100%;
  color: #fff;
}

.partners-swiper .swiper-slide {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0 auto;
  min-height: 33.375rem;
}

.partners-swiper .swiper-button-next {
  width: 6.25rem;
  height: 6.25rem;
  background-image: url("../img/sprite.svg#arrow-next-slide");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 1;
}

.partners-swiper .swiper-button-next:hover {
  cursor: pointer;
}

.partners-swiper__avatar {
  width: 12.5rem;
  height: 12.5rem;
  border-radius: 50%;
  object-fit: cover;
}

.partners-swiper__title, .partners-swiper__subtitle {
  max-width: 21rem;
}

.partners-swiper__title {
  margin-top: 1.875rem;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 2.438rem;
}

.partners-swiper__subtitle {
  margin-top: 0.625rem;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 2.375rem;
  max-width: 20rem;
}

.partners-swiper__info {
  margin-top: 2.375rem;
  max-width: 28.5rem;
  text-align: left;
  font-size: 1.25rem;
  font-weight: 400;
}

.contacts {
  background-color: #fff;
}

.contacts__wrapper {
  width: calc(100% - 9rem);
  margin: 0 auto;
  padding-bottom: 5.25rem;
}

.contacts__title {
  color: #d40f14;
  font-size: 2.25rem;
  font-weight: 900;
  line-height: 3.813rem;
  text-transform: uppercase;
  padding-top: 2.063rem;
  margin: 0 auto;
  width: calc(100% - 9rem);
}

.contacts__content {
  display: flex;
  color: #111;
  width: calc(100% - 9rem);
  margin: 4.375rem auto 0 auto;
}

.contacts__left, .contacts__right {
  width: 50%;
}

.contacts__left {
  display: flex;
  flex-direction: column;
}

.contacts__info {
  display: flex;
  flex-direction: column;
}

.contacts__address, .contacts__phone, .contacts__email {
  font-size: 2.25rem;
  font-weight: 700;
  font-style: normal;
  line-height: 3.75rem;
}

.contacts__phone, .contacts__email {
  text-decoration: none;
  color: #111;
  margin-top: 0.375rem;
  width: max-content;
}

.contacts__map {
  width: 28.5rem;
  height: 17rem;
  box-shadow: 0.375rem 0.375rem 1.25rem rgba(0, 0, 0, 0.25);
  border-radius: 1.25rem;
  margin-top: 2.375rem;
}

.contacts .form {
  display: flex;
  flex-direction: column;
  margin-right: 1.25rem;
}

.contacts .form__name, .contacts .form__email, .contacts .form__message {
  font-size: 1.5rem;
  line-height: 2.438rem;
  font-weight: 700;
  margin-top: 1.25rem;
}

.contacts .form input {
  outline: none;
  border: none;
  border-bottom: 0.125rem solid #111;
}

.contacts .form input,
.contacts .form textarea {
  font-size: 1.125rem;
  line-height: 1.813rem;
}

.contacts .form textarea {
  position: relative;
  height: 100%;
  max-height: 9.125rem;
  resize: none;
  background: url("../img/black-line.png") 0 -0.188rem;
  outline: 0;
  border: none;
}

.contacts .form textarea::placeholder {
  color: rgba(0, 0, 0, 0.7);
}

.contacts .form__distribution, .contacts .form__consent {
  font-size: 0.75rem;
  line-height: 1.188rem;
}

.contacts .form__distribution:hover, .contacts .form__consent:hover {
  cursor: pointer;
}

.contacts .form__distribution input[type='checkbox'], .contacts .form__consent input[type='checkbox'] {
  position: absolute;
  z-index: -10;
  opacity: 0;
}

.contacts .form__distribution span, .contacts .form__consent span {
  display: inline-flex;
  align-items: center;
  user-select: none;
}

.contacts .form__distribution span::before, .contacts .form__consent span::before {
  content: '';
  display: inline-block;
  width: 0.75rem;
  height: 0.75rem;
  border: 0.063rem solid #111;
  margin-right: 0.5em;
  background-color: #111;
}

.contacts .form__distribution input:not(:disabled):active + span::before, .contacts .form__consent input:not(:disabled):active + span::before {
  background-color: #b3d7ff;
  border-color: #b3d7ff;
}

.contacts .form__distribution input:checked + span::before, .contacts .form__consent input:checked + span::before {
  border-color: #111;
  background-color: #d40f14;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fefefe' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

.contacts .form__distribution {
  margin-top: 1.125rem;
}

.contacts .form__consent {
  margin-top: 0.438rem;
}

.contacts .form__consent a {
  font-weight: 700;
  color: #111;
  margin-left: 0.125rem;
}

.contacts .form__submit button {
  color: #fff;
  background-color: #d40f14;
  border-radius: 1.25rem;
  border: none;
  font-size: 1rem;
  font-weight: 700;
  padding: 0.813rem 4rem;
  box-shadow: 0.188rem 0.188rem 0.938rem #00000033;
  outline: 0;
  margin-top: 1.875rem;
}

.contacts .form__submit button:hover {
  cursor: pointer;
}

.contacts__links {
  display: flex;
  margin: 4.688rem auto 0 auto;
  width: calc(100% - 9rem);
}

.contacts .links__column {
  display: flex;
  flex-direction: column;
  width: 80%;
}

.contacts .links__column:last-child {
  width: 20%;
  align-items: center;
  justify-content: center;
}

.contacts .links__row {
  display: flex;
}

.contacts .links__link {
  width: 25%;
  color: #111;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 2.438rem;
  text-decoration: none;
}

.contacts .links__link:nth-child(3) {
  width: 20%;
}

.contacts .social {
  display: flex;
}

.contacts .social a {
  display: block;
}

.contacts .social a:not(:last-child) {
  margin-right: 0.875rem;
}

.contacts .social svg {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  box-shadow: 0.188rem 0.188rem 0.938rem #00000033;
}

@font-face {
  font-family: 'Axiforma';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Axiforma-Regular.woff2") format("woff2"), url("../fonts/Axiforma-Regular.woff") format("woff");
}

@font-face {
  font-family: 'Axiforma';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Axiforma-Bold.woff2") format("woff2"), url("../fonts/Axiforma-Bold.woff") format("woff");
}

@font-face {
  font-family: 'Axiforma';
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: url("../fonts/Axiforma-Black.woff2") format("woff2"), url("../fonts/Axiforma-Black.woff") format("woff");
}

body {
  font-family: 'Axiforma', sans-serif;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

source {
  width: 0;
  height: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 1576px) {
  :root {
    font-size: 18px;
    transition: font-size .3s ease;
  }
}

@media screen and (min-width: 1735px) {
  :root {
    font-size: 19.3px;
    transition: font-size .3s ease;
  }
}

@media screen and (min-width: 1921px) {
  :root {
    font-size: 22px;
    transition: font-size .3s ease;
  }
}

@media screen and (min-width: 2140px) {
  :root {
    font-size: 24px;
    transition: font-size .3s ease;
  }
}

@media screen and (max-width: 1575px) {
  :root {
    font-size: 16.4px;
    transition: font-size .3s ease;
  }
}

@media screen and (max-width: 1440px) {
  :root {
    font-size: 16px;
    transition: font-size .3s ease;
  }
}

@media screen and (max-width: 320px) {
  :root {
    font-size: 12px;
    transition: font-size .3s ease;
  }
}
